html, body {
  height: 100%;
  width: 100%;
  margin: 0;
  overflow: hidden; }

.fullScreen {
  width: 100%;
  height: 100%; }

#objects-region {
  position: absolute;
  right: 10px;
  top: 10px;
  max-height: 100%;
  overflow: auto; }

.card, #search {
  margin: 10px; }

.card {
  font-size: small;
  z-index: 11111;
  max-width: 93% !important;
  min-width: 93% !important; }

.search-icon {
  background: url(images/search-solid.svg) no-repeat top left;
  background-size: contain;
  display: inline-block;
  height: 19px;
  width: 19px;
  margin: auto; }

.type-filter {
  margin: 0 5px; }

#types-filter-region {
  padding: 0 7px; }
